import React from "react"
import { useInView } from 'react-intersection-observer';



const BlogItem = ({ src , date , heading , description}) => {

  const [ref, inView] = useInView({
    triggerOnce:true,
    threshold: 0,
    rootMargin:"50px"
  })

    return(
      <inView className='blog__item-wrapper'>
       <article >
        <img ref={ref} src={inView?src:''}></img>
        <p>{date}</p>
        <h3>{heading}</h3>
        <p>{description}</p>
       </article>
       </inView>
    )
}

export default BlogItem;